import React, { useEffect, useState } from 'react';
import './App.css';
import axios, {AxiosError, AxiosResponse} from 'axios';

const PRODUCTION = '/endpoint';
const DEVELOPMENT = 'http://localhost:5000/endpoint'

const API_CALLS = PRODUCTION;

const App = () : JSX.Element => {
  const [ state, setState] = useState('');

  const getHelloData = async () => {
      await axios({
        method: 'get',
        url: `${API_CALLS}/hello`,
      }).then((result: AxiosResponse) => {
        setState(result.data);
      console.log(result)
      }).catch( (err: AxiosError) => {
      console.log(err);
    })
  }

  const getOtherData = async () => {
    await axios({
      method: 'get',
      url: `${API_CALLS}/hellos`,
    }).then((result: AxiosResponse) => {
      console.log(result);
      setState(result.data);
    }).catch( (err: AxiosError) => {
      console.log(err.response);
      setState(err.response?.data);
  })
}

  const handleButtonClick = () => {
    getOtherData();
  }

  useEffect(() => {
    getHelloData();
  }, []);

  return (
    <div className="App">
      <h1>{state}</h1>
      <button onClick={handleButtonClick}>Change State</button>
    </div>
  );
}

export default App;
